import WatchJS from 'watchjs';
import Timer from './scripts/Timer';
import DoneTasksCounter from './scripts/DoneTasksCounter';
import TaskManager from './scripts/TaskManager';

const { watch } = WatchJS;

const state = {
  setValues: (outOfTurn = false) => {
    state.outOfTurn = outOfTurn;
    state.isDone = false;
  },
};

watch(state, 'isDone', () => {
  if (state.isDone) {
    Timer.timeManager.shortBreak();
    DoneTasksCounter.increaseDoneTasks();
    Timer.stop();
    TaskManager.completeTask();
    state.setValues();
  }
});

watch(state, 'outOfTurn', () => (state.outOfTurn ? Timer.run() : null));

watch(DoneTasksCounter, 'needLongBreak', () => (DoneTasksCounter.needLongBreak ? Timer.timeManager.longBreak() : null));

state.setValues();
TaskManager.init(state);
Timer.init(state);
DoneTasksCounter.init();
